import Photo from './photo.png'
import Github from './github.png'
import './aboutMe.css'
import Check from './check.png'

function importAll(r) {
    let files = {};
    r.keys().map((item, index) => {
        files[item.replace("./", "")] = r(item);
    });
    return files;
}

