import './projects.css';
import Github from './github.png'
import Detail from './detail.png'
import md from "./file/1.md";
import {useEffect, useState} from "react";
import Modal from "../../Modal";
import { useLocation } from "react-router-dom";

function ProjectContents(props) {
    const [detail, setDetail] = useState(false);

    return <div className="projects-contents-info-box">
        <div className="projects-contents-info-title">
            <b className="projects-contents-info-title-text"><b className="projects-due-font">{props.text[0]}</b><br/>{props.text[1]}</b>
        </div>
        <b className="projects-contents-text">{props.text[2]}</b>
        <div className="projects-button-box">
            <a href={props.text[3]} target="_blank" style={{paddingRight:15}}>
                <img src={Github}/>
            </a>

            <a href='javascript:void(0);' style={{overflow:"auto"}} onClick={() => setDetail(!detail)}>
                <img src={Detail}/>
            </a>
            <input id={props.text[0]} style={{display:"none"}} type="button" value="" className="blueBtn" onClick={() => setDetail(!detail)}/>
            {detail &&
                (<Modal md={md} closeModal={() => setDetail(!detail)}>
                </Modal>)}
        </div>
    </div>
}
function LeftSide(props) {
    return <div>
        <img className="projects-image" src={props.imgSrc}/>
        <div className="projects-backboard"/>
        <div className="left-project-info">
            <ProjectContents text={props.text}/>
        </div>
    </div>
}


function importAll(r) {
    let files = {};
    r.keys().map((item, index) => {
        files[item.replace("./", "")] = r(item);
    });
    return files;
}

export function Projects() {
    let project_img = importAll(require.context('./project_img'))
    let count = 0;
    const projectRender = [];
    const text = [];
    text.push(["2023.01.28~2022.02.13"," ", "           .\n    .\n      .", "https://github.com/dev-daru/2023.01-portfolio-react"])
    text.push(["2023.01.05~"," ()", "            ?,       ?\n      ?         ."])
    text.push(["2022.09.01~2022.12.28","  ", "Node.js  BE         . \nORM         ,      .","https://github.com/dev-daru/sandbox-server"]);
    text.push(["2022.03.02~2022.10.01","  ", "      AI       ?     . \nAI      ,    AI       .", "https://github.com/SeniorProjectHoseo2022"])
    text.push(["2022.01.15~2022.01.21","Petmilly", "      ,          . \n      ,           .", "https://github.com/dev-daru/2022.02-Petmily_BackEnd-node.js"])
    text.push(["2022.01.15~2022.01.21","ILookBook", "             . \n           ?      .", "https://github.com/dev-daru/2021.09-ILookBook-node.js"])
    text.push(["2022.03.02~2022.06.10","  ", "    ,           . \n               .", "https://github.com/dev-daru/2021.03-Schedule_Web-Jsp"])
    for (let i in project_img) {
        projectRender.push( <div className="projects-contents-box">
            {<LeftSide key={project_img} num={count+"project"} text={text[count]} imgSrc={project_img[i]}></LeftSide>}
        </div>);
        count++;
    }
    return <div className="projects-box">
        <div className="projects-title-box">
            <div className="projects-title">
                <b className="projects-title-text">PROJECT</b>
            </div>
        </div>
        {projectRender}
    </div>
}
export default Projects;